::-webkit-scrollbar {
    display: none;
}

* {
	box-sizing: border-box;
	font-family: 'Open Sans', sans-serif;
}

html {
	height: 100%;
	min-height: 100%;
}

body {
	font-family: 'Open Sans', sans-serif;
	height: 100%;
	min-height: 100%;
	position: relative;

	&.daytime {
		color: $dark-grey;
	}
}

.container {
	max-width: 1280px;
	width: 100%;
	padding: 40px;
	position: relative;
	margin: 0 auto;
	display: flex;
	justify-content: center;
	align-items: center;
}

.daytime, .nighttime {
	width: 100%;
	height: 100%;
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;

	&:after {
		position: fixed;
		opacity: 0.5;
		width: 100%;
		height: 100%;
		content: '';
		top: 0;
		left: 0;
		z-index: -1;
	}
}

.daytime {
	background-image: url('http://www.williamrajwan.com/weather/assets/img/daytime.jpg');

	&:after {
		background: white;
		opacity: 0.7;
	}
}

.nighttime {
	background-image: url('http://www.williamrajwan.com/weather/assets/img/nighttime.jpg');

	&:after {
		background: #262538;
	}
}
