#forecast__wrapper {
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
  pointer-events: none;
  opacity: 0;
  transition: 2s;
  -webkit-transition: 2s;
  -moz-transition: 2s;

  .search-active & {
    opacity: 1;
  }
}

.location {
  font-size: 36px;
  font-weight: 600;
  text-align: center;
  color: white;
  margin-bottom: 30px;
  width: 100%;
  line-height: 1.4;

  .daytime & {
    color: $dark-grey;
  }

  @media screen and (max-width: $bp-tiny) {
    font-size: 28px;
  }
}
.forecast__item {
  width: 15%;
  color: white;
  position: relative;
  border-right: 1px solid rgba(white, 0.4);
  margin-left: 30px;
  margin-bottom: 40px;
  padding: 20px 30px 20px 0;
  min-width: 200px;

  &:nth-child(2) {
    margin-left: 0;
  }

  &:last-child {
    border-right: none;
    padding: 20px 0;
  }

  .daytime & {
    border-color: rgba($dark-grey, 0.4);
    color: $dark-grey;
  }

  @media screen and (max-width: $bp-desktop) {
    width: 25%;

    &:nth-child(4) {
      border: none;
      padding: 20px 0;
    }

    &:nth-child(5) {
      margin-left: 0;
    }

    &:last-child {
      border-right: none;
      padding: 20px 0;
    }
  }

  @media screen and (max-width: $bp-medium) {
    width: 35%;

    &:nth-child(3) {
      padding: 20px 0;
      border: none;
    }

    &:nth-child(4) {
      margin-left: 0;
      padding: 20px 30px 20px 0;
      border-right: 1px solid;
    }

    &:nth-child(5) {
      margin-left: 30px;
      padding: 0 20px;
      border-right: none;
    }

    &:last-child {
      margin-left: 0;
    }
  }

  @media screen and (max-width: $bp-small) {
    padding: 20px 0;
    width: 70%;
    margin-left: 0;
    border-right: none;
    border-bottom: 1px solid;
    margin-bottom: 10px;

    &:nth-child(3) {
      border-bottom: 1px solid;
    }

    &:nth-child(4) {
      padding: 20px 0;
      border-right: none;
      border-bottom: 1px solid;
    }

    &:nth-child(5) {
      margin-left: 0;
      padding: 20px 0;
      border-right: 0;
    }

    &:last-child {
      border-bottom: none;
    }
  }

  @media screen and (max-width: $bp-tiny) {
    width: 100%;
  }

}
.icon {
  font-size: 70px;
  text-align: center;
  margin-bottom: 40px;
}
.temp__high {
  position: absolute;
  right: 27px;
  font-size: 25px;
  font-weight: 600;
  top: 58px;
}
.temp__low {
  position: absolute;
  top: 88px;
  right: 30px;
  font-size: 14px;
  font-weight: 300;
}
.description {
  text-align: center;
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 2px;
}
.additional__data {
 font-size: 12px;
 font-weight: 400;
 margin-bottom: 10px;
}
.day {
  margin-bottom: 20px;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1.2px;
}
